/** Video **/

.video {
    position: relative;
    max-height: 70vh;
    overflow: hidden;
    .overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        background: linear-gradient(135deg, rgba(137, 28, 30, .8), rgba(158, 55, 242, .6), rgba(255, 255, 255, .5) 100%);
    }
    video {
        display: block;
        width: 100%;
    }
}

.video__contenido {
    display: flex;
    align-items: center;
    height: 100%;
    position: relative;
    z-index: 1;
    h2 {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        color: $blanco;
        font-size: 4rem;
        span {
            background-color: $rosa;
            padding: 0.5rem 1rem;
            font-size: 1.4rem;
            letter-spacing: .2rem;
            text-shadow: $sombra;
        }
    }
    @include telefono2 {
        h2 {
            font-size: 5.5rem;
            span {
                font-size: 1.7rem;
            }
        }
    }
    @include tablet {
        h2 {
            font-size: 7rem;
            span {
                font-size: 2rem;
            }
        }
    }
}