/** Line Up **/

.lineup {
    background-color: $rosa;
    padding: $separacion 0;
    p {
        text-align: center;
        color: $blanco;
    }
    .dia {
        text-transform: uppercase;
        margin: $separacion 0;
    }
}

.escenarios-contenedor {
    padding: 0 $separacion;
    @include telefono2 {
        padding: 0 $separacion *1.5;
    }
    @include tablet {
        padding: 0 $separacion;
        @include grid(2, $separacion);
    }
    @include desktop {
        padding: 0 $separacion *2.5;
    }
    .escenario {
        padding-top: $separacion /1.3;
    }
    .nombre-escenario {
        color: $negro;
        font-weight: $delgada;
        &:after {
            content: '';
            width: 20rem;
            height: 10rem;
            display: block;
            margin: 2rem auto 0 auto;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
        }
    }
}

.escenarios-contenedor .rock {
    margin-bottom: $separacion;
    @include tablet {
        margin-bottom: 0;
    }
}

.edm .nombre-escenario {
    &:after {
        background-image: url(../img/icono_edm.png);
    }
}

.rock .nombre-escenario {
    &:after {
        background-image: url(../img/icono_rock.png);
    }
}

.calendario {
    li {
        padding: $separacion /2;
        font-weight: $bold;
        span {
            font-weight: $regular;
        }
    }
}

.bg-verde .calendario li:nth-child(odd) {
    background-color: darken($verde, 10);
}

.bg-amarillo .calendario li:nth-child(odd) {
    background-color: darken($amarillo, 10);
}