/** Boletos **/

.boletos {
    padding: $separacion;
    .plan {
        p {
            color: $blanco;
            text-align: center;
            margin-bottom: $separacion /2;
        }
    }
}

.boletos__contenedor {
    p {
        padding-top: $separacion;
    }
    @include tablet {
        @include grid(2, $separacion);
        padding: 0 $separacion /2;
    }
    @include desktop {
        padding: 0 $separacion *2;
    }
}

.pase {
    width: 80%;
    margin: 0 auto;
    border-spacing: 0;
    @include tablet {
        width: 100%;
    }
    tbody,
    tfoot {
        td {
            padding: $separacion /4 $separacion /2;
        }
    }
    thead {
        background-color: $rosa;
        color: $blanco;
        text-transform: uppercase;
        th {
            padding: $separacion / 4;
        }
    }
    tbody {
        background-color: $blanco;
        color: $negro;
        td {
            border-bottom: 1px solid #e1e1e1;
        }
        tr {
            &:last-of-type {
                td {
                    border-bottom: none;
                }
            }
        }
    }
    tfoot {
        background-color: $morado;
        color: $blanco;
        font-size: 2.2rem;
        text-align: center;
    }
}