.header {
    background-color: $verde;
    .header__contenido {
        padding: $separacion / 2 0;
        text-align: center;
        @include tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    h1 {
        color: $blanco;
        font-size: 3rem;
        margin-bottom: 1.5rem;
        @include tablet {
            margin-bottom: 0;
        }
    }
    &.fijo {
        position: fixed;
        width: 100%;
        box-shadow: 0 .3rem .5rem #444;
    }
}

.header__navegacion {
    display: flex;
    justify-content: center;
    gap: 2rem;
    a {
        color: $blanco;
        font-size: 1.8rem;
        &:hover {
            color: $amarillo;
        }
    }
}