/** Galería **/

.galeria {
    padding: $separacion;
    h3 {
        text-align: center;
        margin-bottom: $separacion;
    }
    @include desktop {
        padding: $separacion $separacion *2.5;
    }
}

.galeria-imagenes {
    img:hover {
        cursor: pointer;
    }
    @include grid(2, $separacion);
    @include telefono2 {
        @include grid(3, $separacion /2);
    }
    @include tablet {
        @include grid(4, $separacion /2);
    }
    @include desktop {
        @include grid(6, $separacion /4);
    }
}

.overlay {
    position: fixed;
    background-color: rgba(0, 0, 0, .3);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
}

.btn-cerrar {
    width: 5rem;
    height: 5rem;
    background-color: $blanco;
    display: grid;
    place-items: center;
    border-radius: 50%;
    font-size: 3rem;
    font-weight: $black;
    &:hover {
        cursor: pointer;
    }
}