/**** @Author: Francisco Zapata ****/
/** Globales **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 110rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
  font-weight: 700;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2.6rem;
  text-align: center;
  color: #fff;
  text-shadow: 0.1rem 0.1rem 0.5rem #000;
}

/** Utilidades **/
.bg-verde {
  background-color: #4cb8b3;
}

.bg-amarillo {
  background-color: #fef46d;
}

.header {
  background-color: #4cb8b3;
}

.header .header__contenido {
  padding: 2rem 0;
  text-align: center;
}

@media (min-width: 768px) {
  .header .header__contenido {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.header h1 {
  color: #fff;
  font-size: 3rem;
  margin-bottom: 1.5rem;
}

@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}

.header.fijo {
  position: fixed;
  width: 100%;
  box-shadow: 0 .3rem .5rem #444;
}

.header__navegacion {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.header__navegacion a {
  color: #fff;
  font-size: 1.8rem;
}

.header__navegacion a:hover {
  color: #fef46d;
}

/** Video **/
.video {
  position: relative;
  max-height: 70vh;
  overflow: hidden;
}

.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8), rgba(158, 55, 242, 0.6), rgba(255, 255, 255, 0.5) 100%);
}

.video video {
  display: block;
  width: 100%;
}

.video__contenido {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
}

.video__contenido h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #fff;
  font-size: 4rem;
}

.video__contenido h2 span {
  background-color: #f53756;
  padding: 0.5rem 1rem;
  font-size: 1.4rem;
  letter-spacing: .2rem;
  text-shadow: 0.1rem 0.1rem 0.5rem #000;
}

@media (min-width: 600px) {
  .video__contenido h2 {
    font-size: 5.5rem;
  }
  .video__contenido h2 span {
    font-size: 1.7rem;
  }
}

@media (min-width: 768px) {
  .video__contenido h2 {
    font-size: 7rem;
  }
  .video__contenido h2 span {
    font-size: 2rem;
  }
}

/** Información **/
.informacion {
  margin: 4rem auto;
  padding: 0 4rem;
}

.informacion h3 {
  margin: 2rem 0;
}

.informacion p {
  font-size: 1.6rem;
  margin-top: 2rem;
  line-height: 1.5;
}

.informacion span {
  color: #f53756;
  font-weight: 700;
}

@media (min-width: 600px) {
  .informacion {
    padding: 0 6rem;
  }
}

@media (min-width: 768px) {
  .informacion {
    padding: 0 4rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
  .informacion h3 {
    margin: 2rem 0 4rem 0;
  }
}

@media (min-width: 992px) {
  .informacion {
    padding: 0 10rem;
  }
}

/** Line Up **/
.lineup {
  background-color: #f53756;
  padding: 4rem 0;
}

.lineup p {
  text-align: center;
  color: #fff;
}

.lineup .dia {
  text-transform: uppercase;
  margin: 4rem 0;
}

.escenarios-contenedor {
  padding: 0 4rem;
}

@media (min-width: 600px) {
  .escenarios-contenedor {
    padding: 0 6rem;
  }
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    padding: 0 4rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

@media (min-width: 992px) {
  .escenarios-contenedor {
    padding: 0 10rem;
  }
}

.escenarios-contenedor .escenario {
  padding-top: 3.07692rem;
}

.escenarios-contenedor .nombre-escenario {
  color: #000;
  font-weight: 300;
}

.escenarios-contenedor .nombre-escenario:after {
  content: '';
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0 auto;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.escenarios-contenedor .rock {
  margin-bottom: 4rem;
}

@media (min-width: 768px) {
  .escenarios-contenedor .rock {
    margin-bottom: 0;
  }
}

.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}

.calendario li {
  padding: 2rem;
  font-weight: 700;
}

.calendario li span {
  font-weight: 400;
}

.bg-verde .calendario li:nth-child(odd) {
  background-color: #3b9691;
}

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #fef03a;
}

/** Galería **/
.galeria {
  padding: 4rem;
}

.galeria h3 {
  text-align: center;
  margin-bottom: 4rem;
}

@media (min-width: 992px) {
  .galeria {
    padding: 4rem 10rem;
  }
}

.galeria-imagenes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
}

.galeria-imagenes img:hover {
  cursor: pointer;
}

@media (min-width: 600px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 1rem;
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  background-color: #fff;
  display: grid;
  place-items: center;
  border-radius: 50%;
  font-size: 3rem;
  font-weight: 900;
}

.btn-cerrar:hover {
  cursor: pointer;
}

/** Boletos **/
.boletos {
  padding: 4rem;
}

.boletos .plan p {
  color: #fff;
  text-align: center;
  margin-bottom: 2rem;
}

.boletos__contenedor p {
  padding-top: 4rem;
}

@media (min-width: 768px) {
  .boletos__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    padding: 0 2rem;
  }
}

@media (min-width: 992px) {
  .boletos__contenedor {
    padding: 0 8rem;
  }
}

.pase {
  width: 80%;
  margin: 0 auto;
  border-spacing: 0;
}

@media (min-width: 768px) {
  .pase {
    width: 100%;
  }
}

.pase tbody td,
.pase tfoot td {
  padding: 1rem 2rem;
}

.pase thead {
  background-color: #f53756;
  color: #fff;
  text-transform: uppercase;
}

.pase thead th {
  padding: 1rem;
}

.pase tbody {
  background-color: #fff;
  color: #000;
}

.pase tbody td {
  border-bottom: 1px solid #e1e1e1;
}

.pase tbody tr:last-of-type td {
  border-bottom: none;
}

.pase tfoot {
  background-color: #752f97;
  color: #fff;
  font-size: 2.2rem;
  text-align: center;
}

/** Footer **/
.footer {
  background-color: #000;
  padding: 2rem;
}

.footer p {
  text-align: center;
  color: #fff;
  font-size: 1.4rem;
}
