/** Información **/

.informacion {
    margin: $separacion auto;
    padding: 0 $separacion;
    h3 {
        margin: $separacion / 2 0;
    }
    p {
        font-size: 1.6rem;
        margin-top: 2rem;
        line-height: 1.5;
    }
    span {
        color: $rosa;
        font-weight: $bold;
    }
    @include telefono2 {
        padding: 0 $separacion *1.5;
    }
    @include tablet {
        padding: 0 $separacion;
        @include grid(2, $separacion);
        h3 {
            margin: $separacion / 2 0 $separacion 0;
        }
    }
    @include desktop {
        padding: 0 $separacion *2.5;
    }
}